body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1D2731;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-content: center;
}




.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: white;
}

.welcome {
  font-size: 10em;
  font-family: 'Amatic SC', cursive;
}
.loginPage {
    height: 30vw;
    width: 30vw;
    min-width: 370px;
    min-height: 370px;
    margin: 15px;
    background-color: rgb(241, 223, 115);
    box-shadow: 5px 5px 7px rgba(51, 51, 51, 0.7);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.inputwallbg {
    background-color: rgb(141, 204, 246);
}

.createwallbg {
    background-color: rgb(252, 180, 121);
}

.name {
    font-size: 3.5em;
    font-family: 'Amatic SC', cursive;
}

.content {
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

.nameContent {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nameInput {
    width: 85%;
    height: 20%;
    outline: none;
    font-size: 2em;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    font-family: 'Waiting for the Sunrise', cursive;
    text-align: center;
}

input::-webkit-input-placeholder {
    color:rgb(83, 83, 83);
    font-family: 'Waiting for the Sunrise', cursive;
    text-align: center;
    font-size: 1.2em;
}

.button {
    width: 90%;
    height: 25%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.start {
    height: 25%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

}

.button button {
    background-color: transparent;
    font-size: 1.5em;
    font-family: 'Amatic SC', cursive;
    border: none;
    outline: none;
    cursor: pointer;
}

a {
    text-decoration: none;
    color: black;
}
  

.createWall {
    margin: 5px;
    background-color: transparent;
    outline: none;
    border: none;
    color: rgb(2, 42, 129);
    font-size: 1.2em;
    font-family: 'Amatic SC', cursive;
}

.createWall:hover {
    margin: 5px;
    background-color: transparent;
    outline: none;
    border: none;
    color: white;
    font-size: 1.2em;
    font-family: 'Amatic SC', cursive;
}

.check {
    font-family: 'Amatic SC', cursive;
    color: rgb(116, 0, 0);
    font-size: 1.5em;
}

.loginHint {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    white-space: nowrap;
    align-items: center;
    width:25vw;
}

.newWallDes {
    font-size: 1.5em;
    margin-top: 20px;
}
.newWallName {
    font-size: 3em;
}
.newWall::-webkit-input-placeholder {
    color:rgb(83, 83, 83);
    font-family: 'Waiting for the Sunrise', cursive;
    text-align: center;
    font-size: 1.2em;
}

.newWall {
    font-size: 1.2em;
    width: 100%;
}

/* * {
border: blue solid 1px;
} */


.myButton {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px;
    font-family: 'Baloo Bhai', sans-serif;
}

.myButton button {
    background-color: transparent;
    border: none;
    outline: none;
    padding: 0px;
}

.myButton1 {
    padding: 5px;
}

.myButton1:hover {
    background-color: rgb(63, 63, 63);
    padding: 5px;
    border-radius: 10%;
    cursor: pointer;
}

.hint {
    color: white;
    font-size: 0.5em;
}

.myButton2 {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    font-family: 'Baloo Bhai', sans-serif;
}

.myButton2:hover {
    cursor: pointer;
}

.myButton2 img {
    display: inline-block;
    height: 2.5vw;
    min-height: 38px;
}

.myButton2 div {
    /* width: 10%; */
    margin-left: 10px;
    color: white;
}




.board {
  text-align: center;
  /* height: 100vh;
  width: 100vw; */
  display: flex;
  flex-direction: column;
  align-content: flex-start;
}

.boardBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  height: 5vw;
  padding: 35px;
  padding-top: 30px;
}

.left {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  /* width: 50vw; */
  border-color: white;
  border-width: 10px;
  flex: 0 2;
}
.left img {
  height: 3.5vw;
  min-height: 50px;
}

.posts {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  padding: 25px;
  padding-top: 0px;
}

.onePost {
  background-color: #E9B000;
  height: 15vw;
  min-height: 250px;
  width: 15vw;
  min-width: 250px;
  margin: 13px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.7);
}

.postContent {
  margin: 12%;
  text-align: left;
  font-family: 'Waiting for the Sunrise', cursive;
  font-size: 2em;
  border: none;
  outline: none;
  resize: none;
  height: 76%;
  width: 76%;
  background-color: transparent;
}

.boardRight {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: nowrap;
  width: 85vw;
  /* border-color: white;
  border-width: 10px; */
  /* flex: 0 1; */
  color: White;
  font-family: 'Baloo Bhai', sans-serif;
  font-size: 1.2em;
  padding: 20px;
}

.wallDes {
  text-align: left;
}

.wallTitle {
  margin-right: 100px;
  font-size: 1.9em;
  color: #E9B000;
}

.board .hint {
  visibility: hidden;
}
