.inputField textarea {
    border: none;
    outline: none;
    resize: none;
    /* overflow: hidden; */
    height: 30vw;
    width: 30vw;
    font-size: 3em;
    background-color: transparent;
    font-family: 'Waiting for the Sunrise', cursive;
}

textarea::-webkit-input-placeholder {
    color:rgb(129, 106, 0);
    font-family: 'Waiting for the Sunrise', cursive;
    text-align: center;
}





